import { Component, OnInit } from '@angular/core';
import { DataService } from '../data/data.service';

import { ServiceMyHttp } from '../services/my.http.service';
import { ToastrService } from 'ngx-toastr';
import { MatDialog } from '@angular/material';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  constructor(private ServiceMyHttp: ServiceMyHttp,
    private toastr: ToastrService,
    public dialog: MatDialog) {
  }

  displayedColumns = ['nombre', 'email', 'delete'];
  dataSource = null;;

  async ngOnInit() {
    this.dataSource = await this.ServiceMyHttp.Get('/user', null);
  }

  async delete(item) {

    if (confirm("Are you sure to delete " + item.nombre)) {

      const borrado = await this.ServiceMyHttp.Delete('/user', item.id);
      if (borrado) {
        this.dataSource = await this.ServiceMyHttp.Get('/user', null);
        this.toastr.success('Correcto', 'Borrado correctamente', {
          timeOut: 3000
        });
      }
      else {
        this.toastr.error('No se ha podido borrar!', 'Error!', {
          timeOut: 0
        });
      }

    }



  }


}
